.header{
   display: flex;
   justify-content: flex-start;
   background-image: linear-gradient(to right,#672280,#a626d3);
    padding: 20px;
}
.logo{
    width: 179px;
 
}
main{
    padding: 36px;
}
.inputDiv{
   margin: 0;
   display: flex;  
   flex-direction: column;
   justify-content: center;
   gap: 17px;
  

}
.firstLine {
   display: flex;
   flex-direction: 1 1 auto;
   gap: 17px;
   width: 100%;
justify-content: space-around;
flex-wrap: wrap;
}
.secondLine{
    width: 100%;
}
.formInput{
    display: 100%;
    border-radius: 3px;
    border: 1px solid #d5d4d8;
    flex: 1;
    padding: 10px;
    text-indent: 5px;
    
}

.memeButton{
    background-image: linear-gradient(to right,#672280,#a626d3);
    font-family: 16px;
    font-weight: 700;
    color: white;
    padding-top: 10px;
    padding-bottom: 10px;
    border: none;
    width:  100%;
    cursor: pointer;
}

.meme{
    position: relative;
    margin-top: 10px;

   
}
.meme--image{
    width: 100%;

}

.meme--text{
    text-transform: uppercase;
    transform: translateX(-50%);
    width: 80%;
    left: 50%;
    position: absolute;
    text-align: center;
    letter-spacing: 1px;
    color: white;
}
.top{
    top: 0;
}
.bottom{
    bottom: 0;
}